<template>
  <div>
    <p></p>
    <ul>
      <li v-for="(item,index) in radioList" :key="index" :class="selectedNum==index?'active':''" @click="select(index)">{{item}}</li>
    </ul>

    <p></p>
    <ul>
      <li v-for="(item,index) in checkboxList" :key="item" :class="checkbox.includes(index)?'active':''" @click="check(index)">{{item}}</li>
    </ul>
    <button @click="letsGetThisFuckingCheck">{{isCheckAll?'':''}}</button>
    <button @click="checkOpposite"></button>
  </div>
</template>

<script>
export default {
  components: {},

  data() {
    return {
      selectedNum:"",
      radioList: ["", "", "", "", ""],
      checkbox:[],
      checkboxList:["1", "2", "3", "4", "5","6", "7"],
    };
  },

  computed: {
    //
    isCheckAll(){
      if(this.checkbox.length==this.checkboxList.length){
        return true;
      }
      return false;
    }
  },

  methods: {
    //
    select(i) {
      this.selectedNum = i;
    },
    //
    check(i){
      var idx = this.checkbox.indexOf(i);
      //
      if(idx>-1){
        this.checkbox.splice(idx,1);
      }else{
        this.checkbox.push(i);
      }
    },

    letsGetThisFuckingCheck(){
      if(this.isCheckAll){
        this.clearCheckbox();
      }else{
        this.checkAll()
      }
    },
    //
    checkAll(){
      var len = this.checkboxList.length;
      this.checkbox = [];
      for(var i=0;i<len;i++){
        this.checkbox.push(i);
      }
    },
    //
    clearCheckbox(){
      this.checkbox = [];
    },
    //
    checkOpposite(){
      console.log(1)
      var len = this.checkboxList.length;
      var idx;
      for(var i=0;i<len;i++){
        idx = this.checkbox.indexOf(i)
        //
        if(idx>-1){
          this.checkbox.splice(idx,1);
        }else{
          this.checkbox.push(i);
        }
      }
    }
  }
};
</script>
<style scoped>
li{
  display: inline-block;
  font-size: 16px;
  padding: 5px;
  background-color: #e6e6e6;
  margin: 5px 10px;
  cursor: pointer;
}
.active {
  border: 2px solid red;
}
</style>



